import voice_n from './sound/voice/ん.mp3';

//effect import
import effect_cancel from './sound/effect/cancel.mp3';
import effect_decision from './sound/effect/decision.mp3';
import effect_select from './sound/effect/select.mp3';
import effect_taiko from './sound/effect/taiko.mp3';
import effect_taiko2 from './sound/effect/taiko2.mp3';

const AnnounceStore = {
    nanmai: announce_nanmai,
    mairimasu: announce_mairimasu
}

const VoiceStore = {
    a: voice_a,
    i: voice_i,
    u: voice_u,
    e: voice_e,
    o: voice_o
}

const EffectStore = {
    cancel: effect_cancel,
    decision: effect_decision,
    select: effect_select,
    taiko2: effect_taiko2
}

class AnnouncePlayer {
    constructor() {
        this.onPlaying = false;
    }
    play(character) {
        return new Promise(resolve => {
            if (this.onPlaying) {
                this.playing.pause();
            }
            this.playing = new Audio(AnnounceStore[character]);
            this.playing.addEventListener('ended', () => {
                console.log(`ended: [${character}]`);
                this.onPlaying = false;
                resolve();
            });
            this.playing.play();
            this.onPlaying = true;
        });
    }
    stop() {
        if(typeof this.playing != "undefined"){
            this.playing.pause();
            this.playing.currentTime = 0;
        }
    }
}

function getRandom(min, max){
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min)) + min;
}

class VoicePlayer {    
    constructor() {
        this.onPlaying = false;
    }
    static CARD = Object.keys(VoiceStore);
    setvoicetype(voiceType) {
        this.voiceType = voiceType;
    }
    select(character) {
        return new Promise(resolve => {
            if (this.onPlaying) {
                this.playing.pause();
            }
            this.playing = new Audio();
            
            console.log(`this.voiceType: [${this.voiceType}]`);
            if(this.voiceType == 'normal'){
                this.playing.defaultPlaybackRate = 1.0;
            } else {
                var rate = getRandom(1, 3);
                switch(rate){
                    case 1:
                        rate = 0.5;
                        break;
                    case 2:
                        rate = 3;
                        break;
                }
                this.playing.defaultPlaybackRate = rate;
            }
            console.log(`defaultPlaybackRate: [${this.playing.defaultPlaybackRate}]`);
            this.playing.src = VoiceStore[character];
            console.log(`character: [${character}]`);
            resolve();
        });
    }
    play(character) {
        return new Promise(resolve => {
            this.playing.addEventListener('ended', () => {
                this.onPlaying = false;
                resolve();
            });
            this.playing.src = VoiceStore[character];
            this.playing.play();
            this.onPlaying = true;
        });
    }
    replay() {
        this.playing.currentTime = 0;
        this.playing.play();
    }
    stop() {
        if(typeof this.playing != "undefined"){
            this.playing.pause();
            this.playing.currentTime = 0;
        }
    }
}

class EffectPlayer {
    constructor() {
        this.onPlaying = false;
    }
    play(character) {
        return new Promise(resolve => {
            if (this.onPlaying) {
                this.playing.pause();
            }
            this.playing = new Audio(EffectStore[character]);
            this.playing.addEventListener('ended', () => {
                console.log(`ended: [${character}]`);
                this.onPlaying = false;
                resolve();
            });
            this.playing.play();
            this.onPlaying = true;
        });
    }
    stop() {
        if(typeof this.playing != "undefined"){
            this.playing.pause();
            this.playing.currentTime = 0;
        }
    }
}

module.exports = {AnnouncePlayer, VoicePlayer, EffectPlayer};



